let commonUtil = {
    isAllLoaded(scope, cb) {
        var t_img; // 
        var isLoad = true; // 

        // 
        return isImgLoad(cb);

        // 
        function isImgLoad(callback) {
            // 
            $(scope).find('img').each(function() {
                // 0isLoadfalseeach
                if (this.height === 0) {
                    isLoad = false;
                    return false;
                }
            });
            // true0
            if (isLoad) {
                clearTimeout(t_img); // 
                // 
                callback(scope);
                // false
            } else {
                isLoad = true;
                t_img = setTimeout(function() {
                    isImgLoad(callback); // 
                }, 500); // 500
            }
        }
    },
    myScroll(){
      var that = this;
      Vue.directive('scroll', value => {
        if(value) {
          that.isAllLoaded(value, value => {
            new IScroll(value);
          })
        }
      });
    }
};

export default commonUtil;



